ren una nueva forma de\n        viaje. En un futuro, cuando se logre que la práctica se vuelva\n        masiva, esperamos que las personas consideren el compartir el auto\n        como algo viable para aplicar tanto en sus viajes ocasionales de fin\n        de semana como a los de rutina, ir al trabajo, escuela, etc. También\n        esperamos que más conductores se dejen llevar en auto ajeno y dejen\n        el suyo guardado.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        En tercer lugar, compartir el auto implica un beneficio económico\n        para los viajeros. Cuando se finaliza un viaje se calcula el costo\n        de combustible y peajes y se divide por la cantidad de ocupantes del\n        vehículo. Entonces, cuando aumenta el número de ocupantes, se\n        disminuye el costo del viaje y el mismo se vuelve más accesible.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        El carpooling tiene diferentes impactos positivos sobre la sociedad,\n        alienta la sociabilización entre desconocidos, disminuye la cantidad\n        de autos circulantes con los beneficios que esto significa y\n        representa un beneficio económico para los usuarios. Por eso creemos\n        importante alentar esta práctica y concientizar a las personas para\n        que hagan un uso más racional del automóvil.\n    ")]),_vm._v(" "),_c('h2',[_vm._v("Impacto ambiental")]),_vm._v(" "),_c('p',[_vm._v("\n        Cada vez que dos automovilistas deciden compartir el viaje en un\n        solo vehículo, se evitan – en promedio, por kilómetro recorrido – la\n        emisión de 150 gr de CO\n        "),_c('sub',[_vm._v("2")]),_vm._v("\n        a la atmósfera, uno de los principales gases que contribuyen al\n        efecto invernadero, y el consumo de 0,071 litros de nafta, fuente de\n        energía no renovable y que por ello debe utilizarse de forma\n        racional.\n    ")]),_vm._v(" "),_c('h2',[_vm._v("Premios")]),_vm._v(" "),_c('ul',{staticClass:"list"},[_c('li',[_vm._v("Ashoka - “Avancemos” (2011 y 2012)")]),_vm._v(" "),_c('li',[_vm._v("Gobierno de la Provincia de Santa Fe - “Ingenia 3” (2012)")]),_vm._v(" "),_c('li',[_vm._v("\n            Seleccionado por el Programa “Jóvenes Líderes Emprendedores\n            Sociales” (Ashoka Avancemos-SAP) (2012)\n        ")]),_vm._v(" "),_c('li',[_vm._v("\n            1º Mención del Premio Schiffrin, 12ª Convocatoria “Innovación\n            por el Ambiente” de la fundación FARN Argentina (2013)\n        ")]),_vm._v(" "),_c('li',[_vm._v("Espacio Santafesino (2016)")])]),_vm._v(" "),_c('div',{staticStyle:{"cursor":"default"},attrs:{"aria-hidden":"true"}},[_vm._v("\n        _____________________________\n    ")]),_vm._v(" "),_c('img',{staticClass:"img-ES",attrs:{"src":"/static/img/espacio_santafesino.jpg","alt":"Realizado con el apoyo de Espacio Santafesino, Ministerio de Innovación y Cultura de Santa Fe. Convocatoria 2016."}})])
